.div-music {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}

.track-name {
  font-family: Epilogue , sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 22.55px;
}

.audio-component {
  border-radius: 50px;
  color: #f0f2f5;
  height: 66px;
  margin-right: 10px;
  width: 438px;
}

.div-track-name {
  margin-right: 10px;
  text-align: right;
  width: 300px;
}

.btn-favorite {
  background-color: white;
  border: none;
  color: red;
  cursor: pointer;
  font-size: 25px;
}

.loading-music-card {
  font-family: Epilogue , sans-serif;
  font-size: 30px;
  line-height: 65px;
}
