.container {
  align-items: center;
  box-shadow: 2px 5px 8px 3px #888;
  display: flex;
  flex-direction: column;
  height: 400px;
  justify-content: center;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 40%;
}

.input-login {
  font-size: 20px;
  height: 30px;
  width: 60%;
}

.button-login {
  background-color: blue;
  color: white;
  cursor: pointer;
  font-size: 20px;
  margin-top: 10px;
  padding: 5px;
  width: 61%;
}

.logo {
  height: 150px;
  left: 50%;
  position: absolute;
  top: 15%;
  transform: translate(-50%, -50%);
}

.loading-login {
  font-family: Epilogue , sans-serif;
  font-size: 60px;
  left: 50%;
  line-height: 65px;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}
