.user-logged {
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
}

.logo-header {
  height: 65px;
}

h2 {
  margin-right: 20px;
}

.nav-header {
  display: flex;
  list-style: none;
  padding: 0;
  width: 100%;
}

.link-header {
  align-items: center;
  background-color: #f0f2f5;
  color: #2fc18c;
  display: flex;
  font-size: 50px;
  height: 78px;
  justify-content: center;
  margin-right: 3px;
  text-decoration: none;
  width: 34%;
}

.nav-header :hover {
  background-color: #ddd;
}
