@import url(https://fonts.googleapis.com/css2?family=Epilogue:wght@300&display=swap);

@font-face {
  font-family: 'GothicA1';
  src: local('GothicA1'), url(../../static/media/GothicA1-Regular.529477ce.ttf) format('truetype');
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
}

.user-logged {
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
}

.logo-header {
  height: 65px;
}

h2 {
  margin-right: 20px;
}

.nav-header {
  display: flex;
  list-style: none;
  padding: 0;
  width: 100%;
}

.link-header {
  align-items: center;
  background-color: #f0f2f5;
  color: #2fc18c;
  display: flex;
  font-size: 50px;
  height: 78px;
  justify-content: center;
  margin-right: 3px;
  text-decoration: none;
  width: 34%;
}

.nav-header :hover {
  background-color: #ddd;
}

.div-music {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}

.track-name {
  font-family: Epilogue , sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 22.55px;
}

.audio-component {
  border-radius: 50px;
  color: #f0f2f5;
  height: 66px;
  margin-right: 10px;
  width: 438px;
}

.div-track-name {
  margin-right: 10px;
  text-align: right;
  width: 300px;
}

.btn-favorite {
  background-color: white;
  border: none;
  color: red;
  cursor: pointer;
  font-size: 25px;
}

.loading-music-card {
  font-family: Epilogue , sans-serif;
  font-size: 30px;
  line-height: 65px;
}

.loading-album {
  font-family: Epilogue , sans-serif;
  font-size: 60px;
  left: 50%;
  line-height: 65px;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.img-album {
  border-radius: 6px;
  height: 290px;
  width: 290px;
}

.section-album-content {
  display: flex;
  left: 50%;
  position: absolute;
  top: 40%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  width: 80%;
}

.div-album-details {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-right: 60px;
}

.album-name {
  font-family: Epilogue , sans-serif;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 36.9px;
  margin-bottom: 10px;
  margin-top: 50px;
}

.artist-name {
  font-family: Epilogue , sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: 24.6px;
}

.text-favorites-musics {
  font-family: Epilogue , sans-serif;
  font-size: 24px;
  line-height: 24.6px;
  margin-left: 170px;
  margin-top: 100px;
  position: absolute;
}

.page-favorites {
  margin-left: 160px;
  margin-top: 186px;
}

.img-favorite-album {
  border-radius: 50px;
  height: 61px;
  width: 61px;
}

.div-album-favorite {
  align-content: center;
  display: flex;
  justify-content: left;
}

.container {
  align-items: center;
  box-shadow: 2px 5px 8px 3px #888;
  display: flex;
  flex-direction: column;
  height: 400px;
  justify-content: center;
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 40%;
}

.input-login {
  font-size: 20px;
  height: 30px;
  width: 60%;
}

.button-login {
  background-color: blue;
  color: white;
  cursor: pointer;
  font-size: 20px;
  margin-top: 10px;
  padding: 5px;
  width: 61%;
}

.logo {
  height: 150px;
  left: 50%;
  position: absolute;
  top: 15%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.loading-login {
  font-family: Epilogue , sans-serif;
  font-size: 60px;
  left: 50%;
  line-height: 65px;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.div-card {
  border: black 1px solid;
  border-radius: 6px;
  display: flex;
  height: 400px;
  margin-bottom: 10px;
  margin-left: 20px;
  width: 290px;
}

.img-card {
  height: 180px;
  width: 290px;
}

h2 {
  font-family: Verdana , sans-serif;
  font-size: 16px;
  font-style: bold;
  margin-left: 10px;
}

h4, h5 {
  font-size: 12px;
  line-height: 18px;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-top: 2px;
}

.link {
  color: #3d495c;
  text-decoration: none;
}

.div-albuns {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-left: 120px;
  margin-right: 120px;
  margin-top: 10px;
}

.input-search {
  border-radius: 6px;
  font-size: 20px;
  height: 45px;
  margin-right: 10px;
  padding: 12px 16px;
  width: 519px;
}

.form {
  align-items: center;
  display: flex;
  height: 300px;
  justify-content: center;
}

.button-search {
  background-color: #003be5;
  border-radius: 2px;
  color: white;
  cursor: pointer;
  font-family: GothicA1 , serif;
  font-size: 16px;
  height: 45px;
  line-height: 24px;
  padding: 8px 32px;
  width: 134px;
}

.div-search {
  margin-right: 10px;
  position: relative;
  text-align: center;
}

.div-search .fa-search {
  color: #dadada;
  font-size: 24px;
  left: 90%;
  position: absolute;
  top: 24px;
}

.loading-search {
  display: flex;
  font-size: 40px;
  justify-content: center;
  margin-top: 40px;
}

.span-result-album {
  display: flex;
  font-family: Epilogue , sans-serif;
  font-size: 60px;
  justify-content: center;
  line-height: 65px;
  top: 590px;
}

.loading-albuns {
  font-family: Epilogue , sans-serif;
  font-size: 60px;
  left: 50%;
  line-height: 65px;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.span-result {
  font-family: Epilogue , sans-serif;
  font-size: 24px;
  margin-left: 145px;
}

.div-not-found {
  display: flex;
  justify-content: center;
  margin-top: 25%;
}

.text-ops {
  font-family: Epilogue , sans-serif;
  font-size: 200px;
  font-weight: 400;
  line-height: 205px;
}

.text-not-found {
  font-family: Epilogue , sans-serif;
  font-size: 60px;
  font-weight: 300;
  line-height: 65px;
  width: 881px;
}

