.input-search {
  border-radius: 6px;
  font-size: 20px;
  height: 45px;
  margin-right: 10px;
  padding: 12px 16px;
  width: 519px;
}

.form {
  align-items: center;
  display: flex;
  height: 300px;
  justify-content: center;
}

.button-search {
  background-color: #003be5;
  border-radius: 2px;
  color: white;
  cursor: pointer;
  font-family: GothicA1 , serif;
  font-size: 16px;
  height: 45px;
  line-height: 24px;
  padding: 8px 32px;
  width: 134px;
}

.div-search {
  margin-right: 10px;
  position: relative;
  text-align: center;
}

.div-search .fa-search {
  color: #dadada;
  font-size: 24px;
  left: 90%;
  position: absolute;
  top: 24px;
}

.loading-search {
  display: flex;
  font-size: 40px;
  justify-content: center;
  margin-top: 40px;
}

.span-result-album {
  display: flex;
  font-family: Epilogue , sans-serif;
  font-size: 60px;
  justify-content: center;
  line-height: 65px;
  top: 590px;
}

.loading-albuns {
  font-family: Epilogue , sans-serif;
  font-size: 60px;
  left: 50%;
  line-height: 65px;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.span-result {
  font-family: Epilogue , sans-serif;
  font-size: 24px;
  margin-left: 145px;
}
