.loading-album {
  font-family: Epilogue , sans-serif;
  font-size: 60px;
  left: 50%;
  line-height: 65px;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.img-album {
  border-radius: 6px;
  height: 290px;
  width: 290px;
}

.section-album-content {
  display: flex;
  left: 50%;
  position: absolute;
  top: 40%;
  transform: translate(-50%);
  width: 80%;
}

.div-album-details {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-right: 60px;
}

.album-name {
  font-family: Epilogue , sans-serif;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 36.9px;
  margin-bottom: 10px;
  margin-top: 50px;
}

.artist-name {
  font-family: Epilogue , sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: 24.6px;
}
