.div-card {
  border: black 1px solid;
  border-radius: 6px;
  display: flex;
  height: 400px;
  margin-bottom: 10px;
  margin-left: 20px;
  width: 290px;
}

.img-card {
  height: 180px;
  width: 290px;
}

h2 {
  font-family: Verdana , sans-serif;
  font-size: 16px;
  font-style: bold;
  margin-left: 10px;
}

h4, h5 {
  font-size: 12px;
  line-height: 18px;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-top: 2px;
}

.link {
  color: #3d495c;
  text-decoration: none;
}
